<template>
    <Form v-if="!loading" :model="formItem" :label-width="120">
      <Row>
        <Col span="24">
          <h3></h3>
          <div class="split"></div>
        </Col>
      </Row>
      <Row>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.username" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <RadioGroup v-model="formItem.sex">
              <Radio label="male"></Radio>
              <Radio label="female"></Radio>
            </RadioGroup>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <DatePicker type="date" placeholder="" :value="formItem.born" format="yyyy-MM-dd"></DatePicker>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.id_card" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.nation" placeholder=""></Input>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.school" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.major" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.education" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
              <Input v-model="formItem.degree" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <Select v-model="formItem.political">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="24">
          <h3></h3>
          <div class="split"></div>
        </Col>
      </Row>
      <Row>
        <Col span="8">
          <FormItem label="">
            <Col span="20">
              <Slider v-model="formItem.work_year" :max="50"></Slider>
            </Col>
            <Col span="4" :style="{textAlign: 'center'}">
              {{formItem.work_year}}
            </Col>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <Input v-model="formItem.title" placeholder=""></Input>
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <Input v-model="formItem.position" placeholder=""></Input>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="8">
          <FormItem label="">
            <Input v-model="formItem.department" placeholder=""></Input>
          </FormItem>
        </Col>
        <!-- <Col span="8">
          <FormItem label="">
            <InputNumber :max="60" :min="1" v-model="formItem.service_time"></InputNumber>
            
          </FormItem>
        </Col>
        <Col span="8">
          <FormItem label="">
            <Input v-model="formItem.now_position" placeholder=""></Input>
          </FormItem>
        </Col> -->
      </Row>
      <Row>
        <Col span="24">
          <h3></h3>
          <div class="split"></div>
        </Col>
      </Row>
      <Row>
        <Col span="24">
          <FormItem
            v-for="(item, index) in formItem.program_list"
            :key="index"
            :label="'' + (index+1)">
            <Row>
              <Col span="4">
                <Input type="text" v-model="item.name" placeholder="" icon="home"></Input>
              </Col>
              <Col span="4" :style="{textAlign: 'center'}">
                <DatePicker type="month" placeholder="" :value="item.startTime" format="yyyy-MM" @on-change="item.startTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <DatePicker type="month" placeholder="" :value="item.endTime" format="yyyy-MM" @on-change="item.endTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <Input type="text" v-model="item.position" placeholder="" icon="briefcase"></Input>
              </Col>
              <Col span="4" offset="1">
                <Button type="error" @click="removePrograme(index)"></Button>
              </Col>
            </Row>
            <Row>
              <Col span="8">
                <FormItem label="" label-position="top" :label-width="60">
                  <Input v-model="item.content" type="textarea" :rows="5" :autosize="{minRows: 5,maxRows: 5}" placeholder=""></Input>
                </FormItem>
              </Col>
              <Col span="7" offset="1">
                <FormItem label="" label-position="top" :label-width="60">
                  <Input v-model="item.remark" type="textarea" :rows="5" :autosize="{minRows: 5,maxRows: 5}" placeholder=""></Input>
                </FormItem>
              </Col>
            </Row>
          </FormItem>
        </Col>
      </Row>
      <FormItem>
        <Row>
          <Col span="4">
            <Button type="primary" @click="addPrograme" icon="plus-round"></Button>
          </Col>
        </Row>
      </FormItem>
      <Row>
        <Col span="24">
          <h3></h3>
          <div class="split"></div>
        </Col>
      </Row>
      <Row>
        <Col span="24">
          <FormItem
            v-for="(item, index) in formItem.education_list"
            :key="index"
            :label="'' + (index+1)">
            <Row>
              <Col span="4">
                <Input type="text" v-model="item.school" placeholder="" icon="monitor"></Input>
              </Col>
              <Col span="4" :style="{textAlign: 'center'}">
                <DatePicker type="month" placeholder="" :value="item.startTime" format="yyyy-MM" @on-change="item.startTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <DatePicker type="month" placeholder="" :value="item.endTime" format="yyyy-MM" @on-change="item.endTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <Input type="text" v-model="item.content" placeholder="" icon="university"></Input>
              </Col>
              <Col span="4" offset="1">
                <Button type="error" @click="removeEducation(index)"></Button>
              </Col>
            </Row>
          </FormItem>
        </Col>
      </Row>
      <FormItem>
        <Row>
          <Col span="4">
            <Button type="primary" @click="addEducation" icon="plus-round"></Button>
          </Col>
        </Row>
      </FormItem>

      <Row>
        <Col span="24">
          <h3></h3>
          <div class="split"></div>
        </Col>
      </Row>
      <Row>
        <Col span="24">
          <FormItem
            v-for="(item, index) in formItem.work_list"
            :key="index"
            :label="'' + (index+1)">
            <Row>
              <Col span="4">
                <Input type="text" v-model="item.company" placeholder="" icon="ios-compose-outline"></Input>
              </Col>
              <Col span="4" :style="{textAlign: 'center'}">
                <DatePicker type="month" placeholder="" :value="item.startTime" format="yyyy-MM" @on-change="item.startTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <DatePicker type="month" placeholder="" :value="item.endTime" format="yyyy-MM" @on-change="item.endTime=$event"></DatePicker>
              </Col>
              <Col span="4">
                <Input type="text" v-model="item.position" placeholder="" icon="person-stalker"></Input>
              </Col>
              <Col span="4" offset="1">
                <Button type="error" @click="removeWork(index)"></Button>
              </Col>
            </Row>
            <Row>
              <Col span="16">
                <FormItem label="" label-position="top" :label-width="60">
                  <Input v-model="item.content" type="textarea" :rows="5" :autosize="{minRows: 5,maxRows: 5}" placeholder=""></Input>
                </FormItem>
              </Col>
            </Row>
          </FormItem>
        </Col>
      </Row>
      <FormItem>
        <Row>
          <Col span="4">
            <Button type="primary" @click="addWork" icon="plus-round"></Button>
          </Col>
        </Row>
      </FormItem>
      <Row>
        <Col span="24">
          <div class="split"></div>
        </Col>
      </Row>
      <FormItem>
          <Button type="primary" :loading="submiting" @click="submit">
            <span v-if="!submiting"></span>
            <span v-else>...</span>
          </Button>
          <Button type="ghost" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
    <Spin v-else-if="!isNew && loading">...</Spin>
</template>
<script>
  import $ from 'jquery';
  import axios from 'axios';

  export default {
    data() {
      let isNew = true;
      if(this.$route.params.id){
        isNew = false;
      }
      return {
        id: this.$route.params.id,
        isNew: isNew,
        submiting: false,
        loading: false,
        formItem: {
          username: '',
          sex: 'male',
          born: '',
          id_card: '',
          nation: '',
          school: '',
          major: '',
          education: '',
          degree: '',
          political: '',
          work_year: 0,
          title: '',
          position: '',
          department: '',
          program_list: [],
          education_list: [],
          work_list: []
        }
      }
    },
    watch: {
      "$route": function(to,from){
        // console.log(to, from);
        let that = this;
        if(to.name == 'resume-add'){
          that.formItem = {
            username: '',
            sex: 'male',
            born: '',
            id_card: '',
            nation: '',
            school: '',
            major: '',
            education: '',
            degree: '',
            political: '',
            work_year: 0,
            title: '',
            position: '',
            department: '',
            program_list: [],
            education_list: [],
            work_list: []
          }
        }else if(to.name == 'resume-edit'){
          that.isNew = false;
          that.getDetail(that.id);
        }
      }
    },
    mounted(){
      let that = this;
      axios.interceptors.request.use(function (config) {
        if(config.url != '/resume/detail'){
          that.submiting = true;
        }
        return config;
      }, function (error) {
        return Promise.reject(error);
      });

      if(!that.isNew){
        that.getDetail(that.id);
      }
    },
    methods: {
      getDetail(id){
        let that = this;
        that.loading = true;
        axios({
          method: 'post',
          url: '/resume/detail',
          data: {
            id: id
          },
          timeout: 10000,
        }).then(res => {
          that.loading = false;
          let resData = res.data;
          if(resData.code == 200){
            resData.data.program_list = eval(resData.data.program_list)||[];
            resData.data.education_list = eval(resData.data.education_list)||[];
            resData.data.work_list = eval(resData.data.work_list)||[];
            that.formItem = resData.data;
          }else{
            that.$Message.error(resData.msg);
          }
        }).catch(res => {
          console.warn(res);
          that.$Message.error('');
        });
      },
      addPrograme() {
        this.formItem.program_list.push({
          name: '',
          startTime: '',
          endTime: '',
          position: '',
          remark: '',
          content: ''
        });
      },
      removePrograme(index) {
        this.formItem.program_list.splice(index, 1);
      },
      addEducation() {
        this.formItem.education_list.push({
          startTime: '',
          endTime: '',
          school: '',
          content: ''
        });
      },
      removeEducation(index) {
        this.formItem.education_list.splice(index, 1);
      },
      addWork() {
        this.formItem.work_list.push({
          startTime: '',
          endTime: '',
          company: '',
          content: '',
          position: ''
        });
      },
      removeWork(index) {
        this.formItem.work_list.splice(index, 1);
      },
      submit(){
        let that = this;
        let params = $.extend(true, {}, that.formItem);
        params.program_list = JSON.stringify(params.program_list);
        params.education_list = JSON.stringify(params.education_list);
        params.work_list = JSON.stringify(params.work_list);
        // console.log(params);
        // return false;
        let url = '/resume/add';
        if(!that.isNew){
          params.id = that.id;
          url = '/resume/update';
        }

        axios({
          method: 'post',
          url: url,
          data: params,
          timeout: 10000,
        }).then(res => {
          let resData = res.data;
          that.submiting = false;
          if(resData.code == 200){
            that.$Message.success('!');
          }else{
            that.$Message.error(resData.msg);
          }
        }).catch(res => {
          that.submiting = false;
          that.$Message.error('');
        });
      }
    }
  }
</script>

<style lang="less">
  .split{
    height: 1px;
    width: 100%;
    border-bottom: 1px solid #e5e8ef;
    margin-top: 5px;
    margin-bottom: 25px;
  }
  .form-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
  }
</style>



